@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap');
@import 'Variables';
// $nice__color: rgb(97, 218, 251);
// $nice__color: rgb(23, 255, 205);

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Comfortaa', cursive;
  // color: rgb(97, 218, 251);
  color: $nice__color;
  outline: none !important;
}
*:focus,
*:active {
  outline: none !important;
}
body{
  background-color: #282c34;
}
.button{
  background: none;
  border: 1px solid $nice__color;
  /* margin: 5px 0; */
  outline: none;
  padding: 3vw 6vw;
  font-size: 4vw;
  border-radius: 5px;
  cursor: pointer;
  &:hover{
    // background: rgba(97, 218, 251, 0.1);
    background: $btn__background__color;
  }
}
.buttonTrue{
  background: none;
  border: 1px solid $greenlight__color;
  color: $greenlight__color;
  outline: none;
  padding: 3vw 6vw;
  font-size: 4vw;
  border-radius: 5px;
  cursor: pointer;
  &:hover{
    background: $greenlight__background__color;
  }
}
// .buttonTrue:hover{
//   background: rgba(79, 197, 0, 0.1);
// }
// .button:hover{
//   // background: rgba(97, 218, 251, 0.1);
//   background: $btn__background__color;
// }
.input{
  padding: 3vw 2vw;
  outline: none;
  font-size: 5vw;
  background: none;
  border: none;
  width: 100%;
  text-align: left;
  display: block;
}
/* .input:focus {outline: none !important;}
.input::-moz-focus-inner {border: 0px !important;} */

ul{
  list-style-type:disc;
}

.myModal {
  z-index: 5;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.4);
  margin: 0 auto;
  /* overflow-y: scroll; */
}
.myModalContent {
  padding: 25px;
  // background: rgba(78, 92, 119, 0.907);
  background: rgb(37, 37, 37);
  border: 1px solid $nice__color;
  border-radius: 5px;
  overflow-y: scroll;
}
.myModal.active {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

@media(min-width: $adaptive__360px){
  .button{
    font-size: 15px;
    padding: 10px 20px;
  }
  .buttonTrue{
    padding: 10px 20px;
    font-size: 15px;
   
  }
  .input{
    padding: 10px 5px;
    outline: none;
    font-size: 17px;
    background: none;
    border: none;
    width: 100%;
    text-align: left;
    display: block;
  }
}
