@import '../../Variables';

.paragraph{
    // padding: 3vw;
    font-size: 4vw;
    margin: 10px 0;
}
.header{
    font-weight: bold;
    font-size: 5vw;
}
.true{
    @extend .paragraph;
    // background: $greenlight__background__color;
    color: $greenlight__color;
}
.true span{
    color: $greenlight__color;
}
.false{
    @extend .paragraph;
    // background: $redlight__background__color;
    color: $redlight__color;
}
.false span{
    color: $redlight__color;
}
@media(min-width: $adaptive__360px){
    .header{
        font-size: 18px;
        font-weight: bold;
    }
    .paragraph{
        // padding: 10px;
        font-size: 15px;
    }
}
