@import '../../Variables';
// $nice__color: rgb(23, 255, 205);

.navbar{
    margin: 0 0 30px;
    // height: 50px;
    background: rgb(24, 24, 24);
    display: flex;
    justify-content: start;
    overflow-x: scroll;
    scrollbar-width: none;
    // display: grid;
    // grid-template-columns: repeat(5,1fr);
    // gap: 10px;
    &::-webkit-scrollbar {
        display: none;
      }
}
.aWrap{
    text-align: center;
    border: 1px solid $nice__color;
    background: none;
    padding: 3vw 6vw;
    font-size: 4vw;
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
    &:hover{
        // background: rgba(97, 218, 251, 0.1);
        background: $btn__background__color;
    }
}
.a{
    text-decoration: none;
    text-align: center;
    width: fit-content;
    white-space: nowrap
    // min-width: 15%;
}
@media(min-width: $adaptive__360px){
    .aWrap{
        padding: 10px 20px;
        font-size: 15px;        
    }
}
@media(min-width: $adaptive__768px){
    .aWrap{
        // padding: 10px 20px;
        font-size: 16px;        
    }
}