// $nice__color: rgb(23, 255, 205);
@import '../../Variables';

.ulWrap{
    // padding-left: 30px;
    color: rgb(241, 62, 62);
}
.answersItems{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    margin: 20px 0 30px;
    // max-width: 800px;
}
.answersItem{
    margin: 0 auto;
    // width: 96%;
    width: 100%;
    height: 100%;
    & button{
        text-align: left;
    }
}
.buttonAnswer{
    background: none;
    border: 1px solid $nice__color;
    width: 100%;
    font-size: 4vw;
    outline: none;
    padding: 3vw 6vw;
    border-radius: 5px;
    height: 100%;
    cursor: pointer;
    &:hover{
        background: $btn__background__color;
      }
}
.question{
    text-align: center;
    font-size: 5vw;
}
.questionContainer{
    max-width: 800px;
    margin: 0 auto;
    padding: 10px;
}
.button__redlight{
    background: $redlight__background__color;
    border: 1px solid $redlight__color;
    color: $redlight__color;
    &:hover{
        background: $redlight__background__color;
    }
}
.button__greenlight{
    background: $greenlight__background__color;
    border: 1px solid $greenlight__color;
    color: $greenlight__color;
    &:hover{
        background: $greenlight__background__color;
    }
}
.restartDiv{
    background: rgb(24, 24, 24);
    width: fit-content;
    padding: 15px;
    border-radius: 5px;
}
.p{
    margin: 10px 0;
    font-size: 4vw;
}
@media(min-width: $adaptive__360px){
    .p{
        font-size: 15px;
    }
    .question{
        font-size: 18px;
    }
    .buttonAnswer{
        padding: 10px 20px; 
        font-size: 15px;
    }
}

@media(min-width: $adaptive__768px){
    .question{
        font-size: 19px;
    }
    .buttonAnswer{
        font-size: 16px;
    }
}
